"use client";

import React, { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const InstantQuoteForm = () => {
  const [selectedCourse, setSelectedCourse] = useState("");
  const [deliveryDate, setDeliveryDate] = useState(null);
  const [numPeople, setNumPeople] = useState(12);

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log({ selectedCourse, deliveryDate, numPeople });
  };

  return (
    <section className="qt-sec py-10 bg-gray-900 text-white">
      <div className="container mx-auto text-center">
        <h2 className="text-2xl font-bold">Instant Quote - Instant Booking</h2>
        <form className="mt-6 space-y-4" onSubmit={handleSubmit}>
          <div>
            <label>Select your Face-to-Face training</label>
            <select
              className="form-control w-full p-2 text-black"
              value={selectedCourse}
              onChange={(e) => setSelectedCourse(e.target.value)}
            >
              <option value="">Select Face-to-Face training</option>
              <option value="Epilepsy with rescue medication">
                Epilepsy with rescue medication
              </option>
              <option value="First Aid at work">First Aid at work</option>
              <option value="Emergency First Aid at Work">
                Emergency First Aid at Work
              </option>
            </select>
          </div>
          <div>
            <label>Delivery Date</label>
            <DatePicker
              selected={deliveryDate}
              onChange={(date) => setDeliveryDate(date)}
              className="form-control w-full p-2 text-black"
              placeholderText="Select a delivery date"
              minDate={new Date()}
            />
          </div>
          <div>
            <label>Number of People</label>
            <select
              className="form-control w-full p-2 text-black"
              value={numPeople}
              onChange={(e) => setNumPeople(e.target.value)}
            >
              {[...Array(100).keys()].map((num) => (
                <option key={num + 1} value={num + 1}>
                  {num + 1}
                </option>
              ))}
            </select>
          </div>
          <button type="submit" className="btn bg-blue-500 text-white p-2">
            Get Instant Quote
          </button>
        </form>
      </div>
    </section>
  );
};

